<template>
  <div class="playlist">
    <div class="one">
      <img class="bg" :src="imgurl" alt />
      <div class="main">
        <div class="left">
          <img :src="imgurl" alt />
          <p class="p1">
            <i class="erji2">&#xe607; {{ parseInt(num / 10000) + "万" }}</i>
          </p>
          <p class="p2">歌单</p>
        </div>
        <div class="rights">
          <p>{{ jieshao }}</p>
          <div class="man">
            <img :src="touxiang" alt />
            {{ nicheng }}
          </div>
        </div>
      </div>
    </div>
    <div class="twos">
      <div class="title">
        <span>标签:</span>
        <em v-for="(item, index) in tags" :key="index">{{ item }}</em>
      </div>
      <div class="msgshow">
        <div class="msg" ref="abc">
          <span>{{ miaoshu }}</span>
        </div>
        <p>
          <i v-show="!flag" @click="toshow">&#xe61a;</i>
          <i v-show="flag" @click="tohidden">&#xe619;</i>
        </p>
      </div>
    </div>
    <p class="songp">歌曲列表</p>
    <div
      class="threes"
      v-for="(item, index) in song"
      :key="index"
      @click="toMusic(item.id)"
    >
      <div class="tleft">{{ index + 1 }}</div>
      <div class="tmain">
        <p>{{ item.name }}</p>
        <span>
          <em>{{ item.ar[0].name }}</em>
          - {{ item.al.name }}
        </span>
      </div>
      <div class="tright">
        <i>&#xe710;</i>
      </div>
    </div>
    <p class="songp">最新评论</p>
    <div class="four" v-for="(item, index) in user" :key="index">
      <div class="fleft">
        <img :src="item.user.avatarUrl" alt />
      </div>
      <div class="fmain">
        <div class="fmaint">
          <div class="user">
            <p class="fp1">{{ item.user.nickname }}</p>
            <p class="fp2">{{ item.time | toDate }}</p>
          </div>
          <p class="fp3">
            <span v-html="(item.liked = 'false' ? 0 : item.liked)"></span>
            <i>&#xe645;</i>
          </p>
        </div>
        <p class="fp4">{{ item.content }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Playlist",
  data() {
    return {
      flag: false,
      jieshao: "",
      imgurl: "",
      num: "",
      touxiang: "",
      nicheng: "",
      tags: [],
      miaoshu: "",
      song: [],
      username: "",
      like: "",
      date: "",
      words: "",
      userimg: "",
      user: []
    };
  },
  created() {
    const baseUrl = "https://music.tryto.cn";
    let axios = this.$axios;
    let id = this.$route.query.ids;
    //推荐歌单
    axios.get(baseUrl + `/playlist/detail?id=${id}`).then(res => {
      this.jieshao = res.data.playlist.name;
      this.imgurl = res.data.playlist.coverImgUrl;
      this.num = res.data.playlist.playCount;
      this.touxiang = res.data.playlist.creator.avatarUrl;
      this.nicheng = res.data.playlist.creator.nickname;
      this.tags = res.data.playlist.tags;
      this.miaoshu = res.data.playlist.description;
      this.song = res.data.playlist.tracks;
      //   console.log(this.tags)
    });
    axios.get(baseUrl + `/comment/playlist?id=${id}`).then(res => {
      this.user = res.data.comments;
      console.log(this.user);
      console.log(this.user.time);
    });
  },
  filters: {
    //过滤器
    toDate(val) {
      let time = new Date(val),
        // eslint-disable-next-line no-unused-vars
        y = time.getFullYear(),
        m = time.getMonth() + 1,
        d = time.getDate();
      return m < 10 ? `${y}年0${m}月${d}日` : `y年${m}月${d}日`;
    }
  },
  methods: {
    toshow() {
      this.flag = true;
      this.$refs.abc.style.height = "auto";
      this.$refs.abc.style.overflow = "visible";
      this.$refs.abc.style.display = "block";
      console.log(this.$refs.abc.style.height);
    },
    tohidden() {
      this.flag = false;
      this.$refs.abc.style.overflow = "hidden";
      this.$refs.abc.style.display = "-webkit-box";
      console.log(this.$refs.abc.style.height);
    },
    toMusic(ids) {
      this.$router.push({
        path: "/music",
        query: {
          ids: ids
        }
      });
    }
  }
};
</script>

<style scoped>
.playlist {
  width: 100%;
  height: 100%;
}
.one {
  width: 100%;
  height: 360px;
  overflow: hidden;
  position: relative;
}
.bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  filter: blur(60px);
}
.main {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  padding: 60px 30px;
  box-sizing: border-box;
}
.left {
  width: 240px;
  height: 100%;
  position: relative;
  background-color: #fff;
}
.left img {
  width: 100%;
  height: 100%;
}
.left .p1 {
  width: 160px;
  height: 30px;
  position: absolute;
  right: 0px;
  top: 0px;
  z-index: 3;
  color: #fff;
  font-size: 24px;
  background: linear-gradient(
    to left,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 0) 100%
  );
}
.left .p1 i {
  position: absolute;
  top: -40px;
  right: 10px;
  text-shadow: 1px 0 0 rgba(0, 0, 0, 0.15);
}
.p2 {
  position: absolute;
  z-index: 3;
  top: 30px;
  left: 0;
  padding: 0 16px;
  height: 34px;
  color: #fff;
  font-size: 22px;
  text-align: center;
  line-height: 34px;
  background-color: rgba(217, 48, 48, 0.8);
  border-top-right-radius: 34px;
  border-bottom-right-radius: 34px;
}
.rights {
  flex: 1;
  height: 100%;
  color: #fff;
  margin-left: 34px;
  position: relative;
}
.rights p {
  width: 100%;
  font-size: 34px;
}
.man {
  height: 60px;
  display: flex;
  align-items: center;
  position: absolute;
  top: 120px;
  left: 0;
  color: hsla(0, 0%, 100%, 0.7);
  font-size: 28px;
}
.man img {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  margin-right: 10px;
}
.twos {
  width: 100%;
  padding: 10px 30px;
  box-sizing: border-box;
  background-color: #fcfcfd;
}
.title {
  width: 100%;
  height: 50px;
  color: #666;
}
.title span {
  font-size: 28px;
  margin-right: 20px;
}
.title em {
  display: inline-block;
  height: 40px;
  padding: 2px 16px;
  box-sizing: border-box;
  font-size: 24px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 34px;
  margin-right: 20px;
}
.msgshow {
  width: 100%;
  font-size: 28px;
  color: #666;
}
.msg {
  width: 100%;
  height: 110px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.msgshow p {
  width: 100%;
  height: 40px;
  position: relative;
}
.msgshow p i {
  position: absolute;
  right: 0;
  font-size: 40px;
}
.songp {
  width: 100%;
  height: 46px;
  line-height: 46px;
  padding: 0 20px;
  font-size: 24px;
  box-sizing: border-box;
  color: #666;
  background-color: #eeeff0;
}
.threes {
  width: 100%;
  height: 120px;
  display: flex;
  background-color: #fcfcfd;
}
.tleft {
  width: 90px;
  height: 100%;
  line-height: 120px;
  text-align: center;
  color: #999;
}
.tmain {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-bottom: 2px solid #ececec;
}
.tmain p {
  font-size: 34px;
  padding-bottom: 6px;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.tmain span {
  font-size: 24px;
  color: #aaa;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.tright {
  width: 90px;
  height: 100%;
  font-size: 60px;
  color: #aaa;
  line-height: 120px;
  text-align: center;
  border-bottom: 2px solid #ececec;
}
.four {
  width: 100%;
  padding: 30px 20px;
  box-sizing: border-box;
  display: flex;
}
.fleft {
  width: 60px;
  height: 146px;
  padding-right: 20px;
}
.fleft img {
  width: 60px;
  height: 60px;
  border-radius: 100%;
}
.fmain {
  flex: 1;
  border-bottom: 1px solid #eee;
}
.fmaint {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
}
.fp1 {
  color: #666;
  font-size: 28px;
}
.user {
  width: 50%;
  height: 70px;
}
.fp2,
.fp3 {
  color: #999;
  font-size: 19px;
}
.fp3 {
  width: 50%;
  text-align: right;
}
.fp3 i {
  font-size: 40px;
  padding-left: 6px;
}
.fp4 {
  color: #333;
  font-size: 30px;
  width: 100%;
}
</style>
